'use client';
import React, { useEffect, useMemo, useRef, useState } from 'react';
import Timeline, { TimelineMarkers, TodayMarker } from 'react-calendar-timeline';
// import 'react-calendar-timeline/lib/Timeline.css';
if (typeof document !== "undefined" &&
    !document.querySelector('link[href*="react-calendar-timeline"]')) {
  const link = document.createElement("link");
  link.rel = "stylesheet";
  link.href = "https://unpkg.com/react-calendar-timeline@0.30.0/lib/Timeline.css";
  document.head.appendChild(link);
}
import moment from 'moment';
import apiClient from '../lib/api-client';

type WO = {
    id: string;
    doc_no?: string;
    asset_name?: string;
    work_type?: string;
    start_date?: string | null;
    end_date?: string | null;
    description?: string | null;
    raw?: any;
    // optional status mapping
    status?: string | null;
};

function pickColorForWork(w: WO) {
    const wt = (w.work_type || (w.raw && (w.raw.work_type || w.raw.type_work)) || '').toString().toLowerCase();
    const status = (w.raw && (w.raw.doc_status || w.raw.status || w.raw.state) || '').toString().toLowerCase();
    if (status.includes('completed')) return '#10b981';
    if (status.includes('draft')) return '#9ca3af';
    if (wt.includes('preventive') || wt === 'pm') return '#16a34a';
    if (wt.includes('corrective') || wt === 'cm') return '#f97316';
    if (wt.includes('breakdown') || wt.includes('failure')) return '#ef4444';
    return '#3b82f6';
}

export default function TimelineGantt({ pageSize = 2000 }: { pageSize?: number }) {
    const [itemsRaw, setItemsRaw] = useState<WO[]>([]);
    const [selectedDate, setSelectedDate] = useState<string>(moment().format('YYYY-MM-DD'));
    const [loading, setLoading] = useState(false);
    const [selectedItem, setSelectedItem] = useState<WO | null>(null);
    const refTimeline = useRef<any>(null);

    useEffect(() => { load(); }, []);

    async function load() {
        setLoading(true);
        try {
            const res = await apiClient(`/work-orders?q=&page=1&pageSize=${pageSize}`);
            const rows = res?.data ?? res;
            const mapped: WO[] = (rows || []).map((r: any) => ({
                ...r,
                start_date: r.start_date ?? null,
                end_date: r.end_date ?? null,
                description: r.description ?? r.raw?.description ?? '',
                status: (r.raw && (r.raw.doc_status || r.raw.status || r.raw.state)) ?? null
            }));
            setItemsRaw(mapped);
        } catch (err: any) {
            console.error('load timeline', err);
        } finally { setLoading(false); }
    }

    // day window (local)
    const dayStart = useMemo(() => moment(selectedDate + ' 00:00:00'), [selectedDate]);
    const visibleTimeStart = useMemo(() => dayStart.valueOf(), [dayStart]);
    const visibleTimeEnd = useMemo(() => dayStart.clone().add(24, 'hours').valueOf(), [dayStart]);

    // groups: each WO its own group (y-axis) — we want ordering by start_date ascending
    const groups = useMemo(() => {
        // filter items intersecting day and sort by start_date
        const filtered = itemsRaw.filter(i => {
            const s = i.start_date ? moment(i.start_date).valueOf() : null;
            const e = i.end_date ? moment(i.end_date).valueOf() : null;
            if (!s && !e) return false;
            const start = s ?? e!;
            const end = e ?? s!;
            return !(end <= visibleTimeStart || start >= visibleTimeEnd);
        }).sort((a, b) => {
            const sa = a.start_date ? moment(a.start_date).valueOf() : 0;
            const sb = b.start_date ? moment(b.start_date).valueOf() : 0;
            return sa - sb;
        });

        return filtered.map((it, idx) => ({
            id: it.id,
            title: `${it.doc_no ?? it.id} — ${it.asset_name ?? ''}`,
            index: idx
        }));
    }, [itemsRaw, visibleTimeStart, visibleTimeEnd]);

    // items for timeline: clamp to day window
    const items = useMemo(() => {
        const map = new Map(groups.map(g => [g.id, g]));
        const arr: any[] = [];
        groups.forEach(g => {
            const it = itemsRaw.find(x => x.id === g.id);
            if (!it) return;
            const sRaw = it.start_date ? moment(it.start_date).valueOf() : null;
            const eRaw = it.end_date ? moment(it.end_date).valueOf() : null;
            const s = Math.max(sRaw ?? eRaw ?? visibleTimeStart, visibleTimeStart);
            const e = Math.min(eRaw ?? sRaw ?? visibleTimeEnd, visibleTimeEnd);
            if (e <= s) return;
            arr.push({
                id: it.id,
                group: it.id,
                title: `${it.doc_no}`,
                start_time: s,
                end_time: e,
                itemProps: {
                    'data-wo': it.id,
                    style: {
                        background: pickColorForWork(it),
                        color: '#fff',
                        borderRadius: '4px',
                        border: '1px solid rgba(0,0,0,0.12)'
                    }
                },
                canMove: false,
                canResize: false,
                canChangeGroup: false,
                meta: it
            });
        });
        return arr;
    }, [groups, itemsRaw, visibleTimeStart, visibleTimeEnd]);

    // Now indicator: compute if now in range and left position handled by TimelineMarkers/TodayMarker
    const showNow = useMemo(() => {
        const now = moment();
        return now.isBetween(dayStart, dayStart.clone().add(24, 'hours'), null, '[]');
    }, [dayStart]);

    // click handler
    function onItemSelect(itemId: any) {
        const it = itemsRaw.find(i => String(i.id) === String(itemId));
        if (it) setSelectedItem(it);
    }

    return (
        <div style={{ padding: 12 }}>
            <div style={{ display: 'flex', gap: 8, alignItems: 'center', marginBottom: 12 }}>
                <label>
                    Tanggal:
                    <input
                        type="date"
                        value={selectedDate}
                        onChange={e => setSelectedDate(e.target.value)}
                        style={{ marginLeft: 8, padding: '6px 8px' }}
                    />
                </label>

                <button onClick={load} disabled={loading} style={{ marginLeft: 8 }}>Refresh</button>
                <div style={{ marginLeft: 'auto', color: '#666' }}>{items.length} items</div>
            </div>

            <div>
                <Timeline
                    ref={refTimeline}
                    groups={groups}
                    items={items}
                    defaultTimeStart={visibleTimeStart}
                    defaultTimeEnd={visibleTimeEnd}

                    visibleTimeStart={visibleTimeStart}
                    visibleTimeEnd={visibleTimeEnd}
                    //   defaultTimeStart={moment(visibleTimeStart)}
                    //   defaultTimeEnd={moment(visibleTimeEnd)}
                    //   visibleTimeStart={visibleTimeStart}
                    //   visibleTimeEnd={visibleTimeEnd}
                    canMove={false}
                    canResize={false}
                    stackItems={false}
                    itemHeightRatio={0.7}
                    minZoom={1000 * 60 * 30} // 30 minutes
                    maxZoom={1000 * 60 * 60 * 24}
                    onItemSelect={onItemSelect}
                >
                    <TimelineMarkers>
                        {showNow && <TodayMarker date={moment()} />}
                    </TimelineMarkers>
                </Timeline>
            </div>

            {/* modal */}
            {selectedItem && (
                <div style={{
                    position: 'fixed', left: 0, right: 0, top: 0, bottom: 0, background: 'rgba(0,0,0,0.4)', display: 'flex', alignItems: 'center', justifyContent: 'center'
                }}>
                    <div style={{ width: 720, background: '#fff', padding: 16, borderRadius: 8 }}>
                        <h3>{selectedItem.doc_no}</h3>
                        <div><strong>Asset:</strong> {selectedItem.asset_name}</div>
                        <div><strong>Type:</strong> {selectedItem.work_type}</div>
                        <div><strong>Start:</strong> {selectedItem.start_date}</div>
                        <div><strong>End:</strong> {selectedItem.end_date}</div>
                        <pre style={{ marginTop: 8, background: '#f7f7f7', padding: 8 }}>{JSON.stringify(selectedItem.raw ?? selectedItem, null, 2)}</pre>
                        <div style={{ textAlign: 'right', marginTop: 8 }}>
                            <button onClick={() => setSelectedItem(null)}>Close</button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
}
